.card-container {
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
}

.card {
  margin: 10px;
  width: 300px;
  min-height: 300px;
  height: 100%;
  background: var(--theme-background-light);
  color: var(--theme-red);
  padding-bottom: 10px;
}

.card-title {
  color: var(--theme-yellow);
  text-align: center;
  font-family: 'Playfair Display', "Times New Roman", Times, serif;
  font-size: 1.7rem;
  padding: 10px;
}

.card-image {
  animation: fadeIn 1.5s;
}

.card-content {
  display: flex;
  flex-direction: column;
  align-items: center;
  font-size: 1.2rem;
  padding: 5px;
  text-align: center;
}

.two-column {
  column-count: 2;
}

.two-column div {
  text-align: center;
  overflow: hidden;
  word-wrap: unset;
  word-break: keep-all;
}

.card a {
  height: 0px;
  margin-top: -50px;
  margin-bottom: -50px;
}

@keyframes fadeIn {
  0%, 20% { opacity: 0 }
  100% { opacity: 1 }
}